import { Component, OnInit} from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { NgForm } from '@angular/forms';

@Component({
  selector: 'app-diet-plan',
  templateUrl: './diet-plan.component.html',
  styleUrls: ['./diet-plan.component.css']
})
export class DietPlanComponent implements OnInit {
  // @ViewChild('f', { static: false }) signupForm: NgForm;
  planed:Boolean = false;
  cals:number;
  diet:string="";
  exclude:string="";
  response2:any;
  calculated:boolean = false;
  age:number;
  weight:number;
  height:number;
  feet:number;
  inches:number;
  activity:string;
  gender:string="";
  goals:string;
  bmr:number;
  maintenance:number;
  golCals:number;
  day:string;
  btt1:Boolean = false;
  btt2:Boolean = false;
  btt3:Boolean = false;
  btt4:Boolean = false;
  btt5:Boolean = false;
  btt6:Boolean = false;
  btt7:Boolean = false;
  created:Boolean = false;
  imagUrl:string="https://spoonacular.com/recipeImages/";

  constructor(private http: HttpClient) { }

  ngOnInit() {
  }
  //https://api.spoonacular.com/mealplanner/generate?apiKey=0b07353eee4c49c1b14ef72cc3d1029b&targetCalories=2600
  //targetCaloreis=2000 || diet=vegetarian || exclude=eggs
  //Full Meal Plan for 1 week according to cals and diet

  onSubmit(form : NgForm)
  {
    this.height = (this.feet * 12) + this.inches;
    if(this.gender == 'female')
    {
      this.bmr = 655 + (4.35 * this.weight) + (4.7 * this.height) - (4.7 * this.age);
    }
    else
    {
      this.bmr = 66 + (6.23 * this.weight) +  (12.7 * this.height) - (6.8 * this.age);
    }
    this.bmr = Math.floor(this.bmr);
    if(this.activity == 'sedentary')
    {
      this.cals = this.bmr * 1.2;
    }
    else if(this.activity == 'light')
    {
      this.cals = this.bmr * 1.375;
    }
    else if(this.activity == 'moderate')
    {
      this.cals = this.bmr * 1.55;
    }
    else if(this.activity == 'active')
    {
      this.cals = this.bmr * 1.725;
    }
    else
    {
      this.cals = this.bmr * 1.9;
    }
    this.maintenance = Math.floor(this.cals);
    if(this.goals == 'lose2')
    {
      this.golCals = this.maintenance - 1000;
    }
    else if(this.goals == 'lose1')
    {
      this.golCals = this.maintenance - 500;
    }
    else if(this.goals == 'maintain')
    {
      this.golCals = this.maintenance;
    }
    else if(this.goals == 'gain1')
    {
      this.golCals = this.maintenance + 500;
    }
    else
    {
      this.golCals = this.maintenance + 1000;
    }
    this.calculated = true;
  }
  plan()
  {
    this.planed = true;
    let obss = this.http.get('https://api.spoonacular.com/mealplanner/generate?apiKey=2ffb8c83e1c6424fbf42c3510b64078f&targetCalories='  + this.golCals + '&diet=' + this.diet + '&exclude=' + this.exclude);
    obss.subscribe((response2)=>{
      this.response2 = response2;
      console.log(this.response2);
    });
  }
bt1(){this.btt1 = true;}
bt2(){this.btt2 = true;}
bt3(){this.btt3 = true;}
bt4(){this.btt4 = true;}
bt5(){this.btt5 = true;}
bt6(){this.btt6 = true;}
bt7(){this.btt7 = true;}
create()
{
  this.created = true;
}
}
